import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IchangeValues } from './../../../../commonSRC/psServices/models/ps-common-interface';
import { OmniCommonService } from './../../../psServices/omni-common/omni-common.service';
import { PsComplexBeneficiaryDetailsExposed } from './ps-complex-beneficiary-details.component.interface';


/* Islam 21012020 */
@Component({
  selector: 'ps-complex-beneficiary-details',
  templateUrl: './ps-complex-beneficiary-details.component.html',
  styleUrls: ['./ps-complex-beneficiary-details.component.scss'],
})
export class PsComplexBeneficiaryDetailsComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: PsComplexBeneficiaryDetailsExposed;

  public allowedBeneficiaryCurrencies: any;
  public countryFlagLocation: string;
  isCardBenef = false;
  defaultOptions: PsComplexBeneficiaryDetailsExposed = {};

  constructor(public commonProv: PsCommonService, public omniPull: OmniPullService, logger: LoggerService
    , private omniCommon: OmniCommonService) {
    super(commonProv, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    
    // set the reference of requestObject in defaultOptions to be same as the one of this.options as the copyobject will 
    // not have same reference for both but standalone objects in each parent object
    if (this.options.requestObject) {
      this.defaultOptions.requestObject = this.options.requestObject;
    }
    
	// set the reference of defaultOptions.benefAccountNumberOptions.requestObject 
	// in defaultOptions to be same as the one of this.options as the copyobject will 
    // not have same reference for both but standalone objects in each parent object
    if (this.options.benefAccountNumberOptions && this.options.benefAccountNumberOptions.requestObject) {
      this.defaultOptions.benefAccountNumberOptions.requestObject = this.options.benefAccountNumberOptions.requestObject;
    }
    this.options.benefAccountNumberOptions.forceTriggerChange = this.options.requestObject && this.options.requestObject['dataSaveId'] ? false : true;
    if ((PsCommonSettings.oper_ID == CommonBussinessConstant.BANK_CARD_BENEFICIARY_OPER_ID)
      || (PsCommonSettings.oper_ID == CommonBussinessConstant.LOCAL_CARD_BENEFICIARY_OPER_ID)) {
      this.isCardBenef = true;
    }
  }
  validateBeneficiary(value: IchangeValues) {
    //modified by ahmed regarding TP#1304939 as if the screen is navigated from payment screen it will include bankFullObj
    //and if the screen is opened from beneficiary managment  the bankFullObj object will be set to be the selected bank object
    if (this.options.requestObject['bankFullObj'] == null) {
      this.options.requestObject['bankFullObj'] = this.options.requestObject['bank'];
    }
    if (this.options.validateBeneficiary) {
      let request = {
        accountNumber: value.newValue
      };
      if (this.options.requestObject && this.options.requestObject['bankFullObj']) {
        request['bankCode'] = this.options.requestObject['bankFullObj'];
      } else if (PsCommonSettings.oper_ID === CommonBussinessConstant.LOCAL_BENEFICIARY_OPER_ID) {
        this.omniCommon.presentInfoAlert('please_choose_a_bank_key');
        this.options.requestObject['accountNumber'] = null;
        return;
      }
      if (this.options.requestObject && this.options.requestObject['swiftCode']) {
        request['swiftCode'] = this.options.requestObject['swiftCode'];
      } else if (PsCommonSettings.oper_ID === CommonBussinessConstant.EXTERNAL_BENEFICIARY_OPER_ID) {
        this.omniCommon.presentInfoAlert('please_choose_a_swift_code_key');
        this.options.requestObject['accountNumber'] = null;
        return;
      }
      this.common.presentLoading();
      this.omniCommon.validateBeneficiary(request).then(result => {
        this.common.dismissLoading();
        CommonUtils.deleteError(this.options.benefAccountNumberOptions.group, this.options.benefAccountNumberOptions.fcName);
        CommonUtils.deleteError(this.options.benefNameOptions.group, this.options.benefNameOptions.fcName);
        
      }).catch(error => {
        this.common.dismissLoading();
        this.options.requestObject[this.defaultOptions.benefAccountNumberOptions.fcName] = null;
        this.options.requestObject[this.defaultOptions.currencyOptions.fcName] = null;
        this.options.requestObject[this.defaultOptions.benefNameOptions.fcName] = null;
        this.options.benefAccountNumberOptions.group.controls[this.defaultOptions.benefAccountNumberOptions.fcName].setErrors({'invalid': true});
        this.options.benefAccountNumberOptions.group.controls[this.defaultOptions.benefAccountNumberOptions.fcName]['oldValue']=null;
        this.options.benefAccountNumberOptions.group.controls[this.defaultOptions.benefAccountNumberOptions.fcName]['innerValue']=null;
        if (error && error.data.outputType === ConstantCommon.OUT_PUT_TYPE_U) {
          CommonUtils.setErrors(this.options.benefAccountNumberOptions.group, this.options.benefAccountNumberOptions.fcName, [{ msgKey: error.data.outputNotification }]);
        } else {
          this.common.logger.log(error);
        }
      });
    }
  }
  onEmptyAccountNum(event) {
    this.onPsEmpty.emit(event);
  }
}
